body {
  margin: 0;
}

.header__content {
  display: flex;
  justify-content: center;
}

.header__btn {
  margin-left: 10px;
}

.header__title {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: 50px;
}

.main__cards {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.img {
  margin: 5px;
  border: 1px solid black;
  border-radius: 5px;
}
